import React, { useState, useEffect } from 'react';
import './LicenseStatus.css';
import { getApiBaseUrl } from '../services/config';

const LicenseStatus = () => {
  const [licenseInfo, setLicenseInfo] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const fetchLicenseStatus = async () => {
    try {
      setLoading(true);
      const apiBase = await getApiBaseUrl();
      const response = await fetch(`${apiBase}/license/status`);
      
      // Check if we got a license expired response
      if (response.status === 403) {
        const errorData = await response.json();
        if (errorData.error === 'license_expired') {
          setLicenseInfo({
            is_valid: false,
            message: errorData.message,
            days_remaining: 0,
            is_beta: true,
            expired_at_runtime: true
          });
          setError(null);
          
          // Trigger app-wide license expiry handling
          window.dispatchEvent(new CustomEvent('licenseExpired', {
            detail: { message: errorData.message, expired_at_runtime: true }
          }));
          
          return;
        }
      }
      
      const data = await response.json();
      
      if (data.status === 'success') {
        setLicenseInfo(data.license);
        setError(null);
        
        // Check if license expired during runtime
        if (data.license && !data.license.is_valid && data.license.expired_at_runtime) {
          window.dispatchEvent(new CustomEvent('licenseExpired', {
            detail: { message: data.license.message, expired_at_runtime: true }
          }));
        }
      } else {
        setError(data.message || 'Failed to get license status');
      }
    } catch (err) {
      setError('Network error: Could not check license status');
      console.error('License status fetch error:', err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchLicenseStatus();
    
    // Check license status every hour
    const interval = setInterval(fetchLicenseStatus, 60 * 60 * 1000);
    
    return () => clearInterval(interval);
  }, []);

  const getLicenseStatusClass = () => {
    if (!licenseInfo || !licenseInfo.is_valid) return 'license-status-error';
    if (licenseInfo.days_remaining <= 3) return 'license-status-critical';
    if (licenseInfo.days_remaining <= 7) return 'license-status-warning';
    return 'license-status-ok';
  };

  const getLicenseIcon = () => {
    if (!licenseInfo || !licenseInfo.is_valid) return '';
    if (licenseInfo.days_remaining <= 3) return '';
    if (licenseInfo.days_remaining <= 7) return '';
    return '';
  };

  const formatExpiryDate = () => {
    if (!licenseInfo?.license_info?.expiry_date) return 'Unknown';
    
    try {
      const expiryDate = new Date(licenseInfo.license_info.expiry_date);
      return expiryDate.toLocaleDateString();
    } catch (err) {
      return 'Invalid date';
    }
  };

  if (loading) {
    return (
      <div className="license-status license-status-loading">
        <span className="license-icon"></span>
        <span className="license-text">Checking license...</span>
      </div>
    );
  }

  if (error) {
    return (
      <div className="license-status license-status-error">
        <span className="license-icon"></span>
        <span className="license-text">License Error</span>
        <div className="license-details">
          <small>{error}</small>
        </div>
      </div>
    );
  }

  if (!licenseInfo) {
    return null;
  }

  return (
    <div className={`license-status ${getLicenseStatusClass()}`}>
      <span className="license-icon">{getLicenseIcon()}</span>
      <div className="license-content">
        <div className="license-main">
          {licenseInfo.is_beta && (
            <span className="license-badge">BETA</span>
          )}
          <span className="license-text">
            {licenseInfo.is_valid 
              ? `${licenseInfo.days_remaining} days remaining`
              : 'License Expired'
            }
          </span>
        </div>
        <div className="license-details">
          <small>
            {licenseInfo.is_valid 
              ? `Expires: ${formatExpiryDate()}`
              : licenseInfo.message
            }
          </small>
        </div>
      </div>
      {licenseInfo.days_remaining <= 7 && licenseInfo.is_valid && (
        <div className="license-warning">
          <small>
             Beta trial ending soon! Thank you for testing SELO DSP.
          </small>
        </div>
      )}
    </div>
  );
};

export default LicenseStatus;
